import * as actionTypes from '../actions/actionTypes';

const initialState = {
    availableUsers: []
};

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_USERS:
            return {
                ...state,
                availableUsers: action.value
            };
        default:
            return state

    }
};

export default reducer;