import * as actionTypes from './actionTypes';
import axios from 'axios';

export const setAvailableUsers = (value) => {
  return {
      type: actionTypes.SET_USERS,
      value: value
  }
};

export const storeUsers = value => {
    return dispatch => {
        axios.get('/getAllUsers')
            .then(result => {
                console.log(result);
                dispatch(setAvailableUsers(result.data.users));
            })
            .catch(err => console.log(err))
    }
};