import React, {Component} from 'react';
import {connect} from "react-redux";
import axios from 'axios';
import classes from './ManageUsers.css';
import ExpandableComponents from '../../components/ExpandableComponents/ExpandableComponents';
import MaterialFab from '../../components/MaterialComponents/MaterialFab/MaterialFab';
import * as actions from '../../store/actions/index';
import AlertDialog from '../../components/AlertDialog/AlertDialog';
import Input from '@material-ui/core/Input';
import {MetroSpinner} from "react-spinners-kit";
import LoadingOverlay from "react-loading-overlay";

class ManageUsers extends Component {

    state = {
        alertDialogOpen: false,
        changePass: '',
        cnfChangePass: '',
        toBeModifiedUser: null,
        loading: false,
        successAlertDialogOpen: false
    };

    onSuccessAlertDialogCloseHandler = () => {
        this.setState({successAlertDialogOpen: false});
    };

    changePassClickHandler = user => {
        if(!user.permissions.accessBeta)
            this.setState({alertDialogOpen: true, toBeModifiedUser: user});
        console.log(user)
    };

    alertDialogCloseHandler = () => {
        this.setState({alertDialogOpen: false, toBeModifiedUser: null});
    };

    changePassConfirmClickHandler = () => {
        if (this.state.changePass === this.state.cnfChangePass) {
            this.setState({loading: true});
            axios.put('/updateUser', {updatedPassword: this.state.changePass, id: this.state.toBeModifiedUser._id})
                .then(res => {
                    this.setState({
                        successAlertDialogOpen: true,
                        alertDialogOpen: false,
                        changePass: '',
                        cnfChangePass: '',
                        loading: false
                    });
                })
        }
    };

    render() {

        const users = this.props.users;
        const expandableComponents = users.map(user => {
            console.log(user.permissions.addQuestion);
            const userDetails = <div className={classes.UserDetailsDiv}>
                <ul className={classes.UserDetails}>
                    <li className={classes.UserDetail}>Username: {user.username}</li>
                    <li className={classes.UserDetail}>Password: {user.password}</li>
                    <li className={classes.UserDetail}>Permissions:</li>
                    <ul className={classes.UserDetails}>
                        <li className={classes.UserDetail}>CreateUser: {user.permissions.createUser ? 'true' : 'false'}</li>
                        <li className={classes.UserDetail}>AddQuestion: {user.permissions.addQuestion ? 'true' : 'false'}</li>
                        <li className={classes.UserDetail}>UpdateQuestion: {user.permissions.updateQuestion ? 'true' : 'false'}</li>
                        <li className={classes.UserDetail}>GeneratePaper: {user.permissions.generateQuestion ? 'true' : 'false'}</li>
                    </ul>
                </ul>
            </div>;
            const expandableActions = <div className={classes.UserControls}>
                <MaterialFab onClick={() => this.changePassClickHandler(user)}>Change Password</MaterialFab>
            </div>;
            const expSummaryComponent = <span className={classes.SummaryComponent}>{user.username}</span>
            return {
                summary: ' ',
                summaryComponent: expSummaryComponent,
                detail: userDetails,
                actions: expandableActions
            }

        });


        return (

            this.state.loading ? <div style={{position: 'absolute', top: '40vh', left: '45vw'}}>
                <LoadingOverlay
                    active={this.state.loading}
                    spinner={<MetroSpinner size={100} sizeUnit='px' color='#313131'/>}
                    text={<span style={{color: '#313131'}}>LOADING...</span>}
                />
            </div> : <div className={classes.ManageUsers}>
                <span className={classes.ContentHeading}>Available Users</span>
                <ExpandableComponents expandableComponentsData={expandableComponents}/>
                <AlertDialog
                    noTitleButton
                    isOpen={this.state.alertDialogOpen}
                    onClose={this.alertDialogCloseHandler}
                    dialogTitle='Enter the password'
                    dialogContentComponent={
                        <div className={classes.AlertDialogContents}>
                            <div style={{display: 'flex', flexFlow: 'row'}}>
                                <span style={{color: '#313131'}}>New Password: </span>
                                <Input
                                    type='password'
                                    autoFocus
                                    fullWidth
                                    value={this.state.changePass}
                                    onChange={(event) => this.setState({changePass: event.target.value})}
                                />
                            </div>
                            <div style={{display: 'flex', flexFlow: 'row', marginTop: '30px'}}>
                                <span style={{color: '#313131', width: '100px'}}>Confirm Password: </span>
                                <Input
                                    type='password'
                                    fullWidth
                                    value={this.state.cnfChangePass}
                                    onChange={(event) => this.setState({cnfChangePass: event.target.value})}
                                />
                            </div>
                        </div>
                    }
                    otherDialogActions={
                        <div className={classes.UserControls}>
                            <MaterialFab onClick={this.changePassConfirmClickHandler}>Confirm</MaterialFab>
                            <MaterialFab onClick={this.alertDialogCloseHandler}>Close</MaterialFab>
                        </div>
                    }
                />
                <AlertDialog
                    isOpen={this.state.successAlertDialogOpen}
                    onClose={this.onSuccessAlertDialogCloseHandler}
                    dialogTitle='Success'
                    dialogContentText='Password Updated Successfully'
                    buttonText='Close'
                    onClickButton={() => {
                        this.props.storeUsers();
                        this.setState({successAlertDialogOpen: false})
                    }}
                />

            </div>

        )
    }
}

const mapStateToProps = state => {
    return {
        users: state.usersReducer.availableUsers
    }
};

const mapDispatchToProps = dispatch => {
    return {
        storeUsers: () => dispatch(actions.storeUsers())
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageUsers);